.auth__button {
    margin: 230px 0 0;
    padding: 0;
    width: 100%;
    height: 50px;
    background: #FFFFFF;
    border-radius: 2px;
    border: none;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    transition: all 0.4s ease-in-out;
}
  
.auth__button:hover {
    cursor: pointer;
    opacity: 0.5;
}